.article {
  width: 925px;
  min-height: 500px;
  padding: 0px 24px;
  color: #4e2c1e;
}

.article h3 {
  font-family: Arial, sans-serif;
  font-size: 24px;
  font-weight: 500;
  padding-bottom: 20px;
}
.article p {
  word-spacing: 11px;
  text-align: justify;
  font-size: 14px;
  line-height: 1.2;
  padding-bottom: 20px;
}

.article p:last-child {
  padding-bottom: 110px;
}
