.header {
  &__nav {
    max-width: 925px;
    min-height: 40px;

    background-color: #4e2c1e;
    background-image: linear-gradient(
      0deg,
      #a4671f 0%,
      #834c0b 10%,
      #77470b 30%,
      #b0782b 61%,
      #e9c087 100%
    );
    box-shadow: 0px 1px 0px 0px #7e4f13;

    text-transform: uppercase;
    color: #ffffff;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
  }
}
.nav {
  &_logo-img {
    width: 284px;
    background-size: cover;
    position: absolute;
    bottom: -95%;
    right: 36%;
    z-index: 10;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 922px;
    border-bottom: 1px solid #77470b;
  }
}
.nav-list {
  &__item {
    background-image: linear-gradient(
      0deg,
      #a4671f 0%,
      #834c0b 10%,
      #77470b 30%,
      #b0782b 61%,
      #e9c087 100%
    );
    height: 40px;

    border: 1px solid #ccc;
    border-right: 1px solid #ccaf86;

    padding: 4px 27px;
    display: flex;
    align-items: center;

    transition: all 0.7s;
  }

  &__item:hover {
    background-color: #d7c074;
    background-image: linear-gradient(
      0deg,
      #faedc7 0%,
      #eacf8f 13%,
      #e8c478 63%,
      #fff9e0 100%
    );
  }

  &__item:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  &__item:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  &__item img {
  }

  &__item a {
  }

  &__link {
    word-wrap: break-word;
    color: #ffffff;
    font-size: 11px;
    font-family: Verdana, sans-serif;
    line-height: 15px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.7s;
  }

  &__link-logo {
    width: 150px;
  }
}

.dropdown-list {
  display: none;
  border: 1px solid #bfa485;
  z-index: 5;
  position: absolute;
  top: 40px;
  left: 109px;
  &__item {
    width: 190px;
    word-wrap: break-word;
    color: #45250a;
    font-size: 11px;
    font-family: Verdana, sans-serif;
    line-height: 15px;
    text-align: left;
    text-transform: capitalize;
    background-color: #fcebc6;
    padding: 5px 11px;
    transition: all 0.3s;
  }
  &__item:hover {
    background-color: #d8af75;
    transition: all 0.5s;
  }

  &__link {
    color: #45250a;
    font-size: 14px;
  }
}

.nav-list__item:hover a.nav-list__link {
  color: #4e2c1e;
}

.nav-list__item:hover .dropdown-list,
.dropdown-list:hover {
  display: block;
}
