.slider {
  display: flex;
  flex-direction: column;

  &__container {
    min-width: 924px;
    min-height: 324px;
    display: flex;
    align-items: center;
    position: relative;
    background-color: #fff7e1;
    filter: drop-shadow(0px 3px 2px rgba(0, 0, 0, 0.3));
    overflow: hidden;

  }

  &__images {
    display: flex;
    align-items: center;
    transition: all 1s ease;
  }

  &__images img {
    width: 925px;
    height: 324px;
    background-size: cover;
    text-indent: -100vw;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 19px;
    min-height: 30px;
    background-color: #fff7e1;
    padding-top: 7px;
  }

  &__button {
    width: 13px;
    height: 13px;
    background-color: rgba(209, 115, 0, 0.5);
    box-shadow: inset 0px 2px 3px 0px rgba(51, 27, 2, 0.74);
    border-radius: 50%;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
  }

  &__button-active-js {
    width: 16px;
    height: 16px;
    background-color: #7c5a4c;
    border: 1px solid #fff;
    border-radius: 50%;
    transition: all 0.2s;
  }
}

.arrow {
  height: 324px;
  cursor: pointer;
  opacity: 0;
  z-index: 3;
}
.left {
  position: absolute;
  top: 0px;
  left: 0px;
  transform: rotate(-180deg);
}
.right {
  position: absolute;
  top: 0px;
  right: 0px;
}

