.personal__gallery {
  max-width: 925px;
  min-height: 200px;
  background-color: #fff7e1;
}

.gallery {
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 37px;
  }

  &__item {
    border: 1px solid #a08357;
    background-color: #ffffff;
    text-align: center;
    overflow: hidden;
    margin: 0px 12px;
  }

  &__item img {
    width: 182px;
    height: 189px;
    object-fit: contain;
    text-indent: -100vw;
  }
}
