.footer {
  width: 925px;
  min-height: 111px;
  background: linear-gradient(
    to bottom,
    rgb(127, 93, 66) 0%,
    rgb(166, 132, 94) 100%
  );

  display: flex;
  flex-direction: column;

    &__top {
    background-color: #4e2c1e;
    background-image: linear-gradient(0deg, #4e2c1e 0%, #4e2c1e 100%);
    color: #ffffff;
    font-size: 11px;
    text-transform: uppercase;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
    box-shadow: 0px 1px 1px 0px rgba(17, 17, 26, 0.86);
    border-top: 1px solid #62483a;
  }

  &__list {
    min-height: 54px;
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;

  }

  &__item a {
    position: relative;
    display: block;
    min-height: 54px;
    font-size: 11px;
    color: #ffffff;
    text-transform: uppercase;
    text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
    line-height: 11px;
    padding:  21px 40px;

  }

  &__item a::after {
    content: "";
    position: absolute;
    top: 21px;
    right: 0px;
    width: 1px;
    height: 10px;
    background-color: #ffffff;
  }

  &__item:nth-child(6n) a::after {
    content: none;
  }

  &__bottom {
    display: flex;
    background: linear-gradient(
      to bottom,
      rgb(127, 93, 66) 0%,
      rgb(166, 132, 94) 100%
    );
    color: #ffffff;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__tel {
    padding-left: 40px;
  }
  &__tel a {
    color: #ffffff;
    font-size: 19px;
    font-family: GaramondC, sans-serif;
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 15px 34px;
    &_img {
    }
  }

  &__logo p {
    font-size: 11px;
  }
}
